import { Method } from "axios";
import { lookupService } from "dns";

const injectImgSetup = {
  install(Vue, options) {
    Vue.prototype.$checkImage = (src: string, duration: number = 150) => {
      return new Promise((resolve, reject) => {
        let img = document.createElement('img');
        let classname = `img-${ new Date().getTime() }`;
        /* img.src = "http://localhost//web-files/proffilms-server/assets/ups/uploadimg/accessory-braids-close-up-2009395.jpg";
        img.src = "http://localhost//web-files/proffilms-server/assets/ups/uploadimg/adult-art-attractive-982610.jpg"; */
        img.src = src;
        // img.width = 100;
        img.style.position = 'absolute';
        img.style.visibility = 'hidden';
        img.setAttribute('class', classname);
        document.body.insertAdjacentElement('beforeend', img);

        let img2 = document.querySelector('.' + classname) as HTMLImageElement;
        let counter = 0;
        let limit = 100;
        setTimeout(function looper() {
          img2 = document.querySelector('.' + classname) as HTMLImageElement;
          let data;
          // SET THE DATA OBJECT FOR THE IMAGE  ..............................
          if(img2) {
            data = {
              width: img2.width,
              height: img2.height,
            }
          }else {
            reject('There seems to be an error with the image url!');
          }
          // IF THE IMAGE WIDTH CAN BE OBTAINED OR NOT  ...................
          if((data.width == 0 || data.height == 0) && counter < limit) {
            setTimeout(() => {
              looper();
              counter ++;
            }, 5);
          }else {
            if((data.width == 0 || data.height == 0) && counter >= limit) {
              img2.remove();
              reject('Still could not get the image Data! Please try again');
            }else {
              img2.remove();
              resolve(data);
            }

          }
        }, duration);

      });
    }
    Vue.prototype.$looper = (cb:Function, duration: number = 50) => {
      setTimeout(function looper() {
        if(cb()) {
          setTimeout(() => {
            looper();
          }, duration);
        }
      }, 0);
    }
  }
}


export const injectImg = injectImgSetup;