



















































export default {
mounted() {
  if(this.$route.name != 'home') {
    this.showNavLogo = true;
    this.$refs.pfHeader.style.backgroundColor = "#1A161C";
  }else {
    this.showNavLogo = false;
  }

  chkShowNav(this);
  window.addEventListener("resize", (e) => {
    chkShowNav(this);
    
  });
  window.addEventListener("touchmove", e => {
    this.hrevealMove(e)
  });

  function chkShowNav(self) {
    let width = window.innerWidth;
    let height = window.innerHeight;
    if(width <= 600) {
      if(self.$route.name != 'home') self.showNav = false;
      self.showMenu = true;
    }else {
      self.showNav = true;
      self.showMenu = false;
    }
  }
},
data: () => ({
  navListing: [{
    path: '/',
    routename: 'home',
    title: 'Home',
    icon: 'house'
  },{
    path: '/gallery',
    routename: 'gallery',
    title: 'Gallery',
    icon: 'camera-3'
  },{
    path: '/videos',
    routename: 'videos',
    title: 'Videos',
    icon: 'youtube-play-outline'
  },{
    path: '/contact',
    routename: 'contact',
    title: 'Contact',
    icon: 'device-mobile'
  },{
    path: '/about',
    routename: 'about',
    title: 'About',
    icon: 'globe-world'
  }],
  showNavLogo: false,
  showNav: true,
  showMenu: true,
  menuActive: false,
  hrevealMovement: {
    x: 0, prevX: 0,
    distance: 0,
    duration: 0,
    start: false,
    trans: true,
    limit: 220
  }
}),
methods: {
  menuClk(e) {
    this.menuActive = !this.menuActive;
    let navMenu = document.querySelector(".navigator-menu") as HTMLElement;
    if(!navMenu) return console.log("error in Navigation Menu - Access Denied!");
    if(this.menuActive) {
      navMenu.style.transform = `translateX(${ 0 }px)`;
    }else {
      navMenu.style.transform = `translateX(${ -250 }px)`;
    }
    // console.log(this.menuActive);
  },
  hrevealStart(e:TouchEvent) {
    // console.log(e);
    let hr = this.hrevealMovement;
    if(this.menuActive) {
      hr.limit = 0;
    }else hr.limit = -220;
    hr.x = e.touches[0].screenX; hr.prevX = hr.x; hr.distance = 0; hr.duration = 0; hr.start = true;

  },
  hrevealMove(e:TouchEvent) {
    let hr = this.hrevealMovement;
    if(!hr.start) return;
    // console.log(e);
    hr.x = e.touches[0].screenX;
    hr.distance = hr.x - hr.prevX;
    hr.duration = 0;
    hr.trans = false;
    let limit = hr.limit;
    setTimeout(() => {
      let navMenu = document.querySelector(".navigator-menu") as HTMLElement;
      if(!navMenu) return console.log("error in Navigation Menu - Access Denied!");
      // console.log("Distance", hr.distance, limit + hr.distance);
      if(hr.distance > -limit) hr.distance = -limit;
      navMenu.style.transform = `translateX(${ limit + hr.distance }px)`;
    }, 0);


  },
  hrevealEnd(e:Event) {
    let hr = this.hrevealMovement;
    setTimeout(() => {
      let navMenu = document.querySelector(".navigator-menu") as HTMLElement;
      if(!navMenu) return console.log("error in Navigation Menu - Access Denied!");
      hr.trans = true;
      
      if(hr.distance > 65) {
        navMenu.style.transform = `translateX(${ 0 }px)`;
        this.menuActive = true;
      }else {
        navMenu.style.transform = `translateX(${ -250 }px)`;
        this.menuActive = false;
      }
    }, 0);
    hr.start = false;
    // console.log(hr);
  }
}
}
