



















export default {
props: ['info', 'accept', 'acceptText'],
mounted() {
  this.info;
  console.log(this.acceptText);
  if(this.acceptText != null && this.acceptText != '') {
    this.acceptBtnText = this.acceptText;
  }else {
    this.acceptBtnText = 'Accept'
  }
},
data: () => ({
  acceptBtnText: ''
}),
methods: {
  acceptMt() {
    this.$emit('accept');
  },
  cancelMt() {
    this.$emit('state', false);
  }
},
}
