import Vue from 'vue';
import Router from 'vue-router';
const Home = () => import('./views/Home.vue');
const Gallery = () => import('./views/Gallery.vue');
const Videos = () => import('./views/Videos.vue');
const Contact = () => import('./views/Contact.vue');
const About = () => import('./views/About.vue');
const Addupdate = () => import('./views/Addupdate.vue');

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/gallery',
      name: 'gallery',
      component: Gallery
    },
    {
      path: '/videos',
      name: 'videos',
      component: Videos
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/addupdate',
      name: 'addupdate',
      component: Addupdate
    }
  ],
});
